import $ from 'jquery';

import BpmnModeler from 'bpmn-js/lib/Modeler';

let modeler = new BpmnModeler({
    container: '#js-canvas'
});

let downloadLink = $('#js-download-diagram');

function createNewDiagram() {
    let promise = openDiagram($('.bpmn').val());
}

async function openDiagram(xml) {
    try {
        await modeler.importXML(xml);
        downloadLink.attr({
            'href': 'data:application/bpmn20-xml;charset=UTF-8,' + xml,
            'download': 'diagram.bpmn'
        })
    } catch (err) {
        console.error(err);
    }
}

$(function () {
    $('#js-show-xml').click(e => {
        e.stopPropagation();
        e.preventDefault();
        $('textarea.bpmn').toggle();
        let show = $(this).text() === 'Show xml';
        $(this).text(show ? 'Hide xml' : 'Show xml');
    });
    createNewDiagram();
    let exportArtifacts = debounce(async function () {
        try {
            const {
                xml
            } = await modeler.saveXML({
                format: true
            });
            $('textarea.bpmn').val(xml);
            let encodedData = encodeURIComponent(xml);
            downloadLink.attr({
                'href': 'data:application/bpmn20-xml;charset=UTF-8,' + encodedData,
                'download': 'diagram.bpmn'
            });
        } catch (err) {
            console.error('Error happened saving XML: ', err);
        }
    }, 500);
    modeler.on('commandStack.changed', exportArtifacts);
});


function debounce(fn, timeout) {
    let timer;
    return () => {
        if (timer) {
            clearTimeout(timer);
        }
        timer = setTimeout(fn, timeout);
    };
}
